.contenedor-btn{
    color:black; 
    background:#2a9e2a;
    text-align:center;
    border-radius:50%;
    width:100px;
    height:100px;
    position:fixed;
    bottom:40px;
    right:20px;
    cursor:pointer;
    box-shadow:3px 3px 5px black;
    border:1px solid #255325;
    box-sizing:border-box;
    opacity: .85;
    transition: opacity .2s;
    z-index: 900;
    font-size: 57px;
    padding: 21px 0;
}
.contenedor-btn:hover{
    opacity: 1;
}

@media screen and (max-width: 500px) {
    .contenedor-btn{
        width: 55px;
        height: 55px;
        font-size: 31px;
        padding: 11px;
    }
}