.container-modal-LineChart{
    position: absolute;
    top: 400px;
    background:#5d7f4ef2;
    height: 190px;
    width: 400px;
    padding: 10px;
    z-index: 1200;
    border-radius: 3px;
    outline: 1px solid black;
    text-align: center;

}
.container-modal-LineChart p{
    font-weight: 800;
    letter-spacing: .5px;
    font-size: 16px;
}
.container-modal-LineChart form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 7px;

}
.container-modal-LineChart form input{
    padding: 4px 0px 4px 10px;
    border-radius: 5px;
    font-size: 12px;
}

.container-modal-LineChart form input[type="color"]{
    width: 20px;
    height: 20px;
    padding: 0;
    position: absolute;
    bottom: 17px;
    left: 110px;
}
.container-modal-LineChart form input[type="submit"]{
    grid-column: 1 / 3;
    width: 100px;
    margin: auto;
    padding: 8px 0px;

}

@media screen and (max-width:500px) {
    .container-modal-LineChart{
        /* top: 0px; */
        /* position: relative; */
        width: 84.5vw;
        /* background-color: red; */
    }

    .container-modal-LineChart form input{
        width: 130px;
    }

    .container-modal-LineChart form input[type="color"]{
        left: 35px;
        width: 50px;
        height: 25px;
    }

}