.Title-Projects{
	text-align: center;
	font-size: 44px;
	margin: 70px 0 -65px;
	color: rgb(223, 217, 217);
}

.container-cards {
	max-width: 900px;
	margin:70px auto;
	position: relative;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	padding: 20px 0px;
	z-index: 10;
	gap: 20px;
}
.container-cards a{
	position: relative;
}
.container_card{
	height: 385px;
    width: 260px;
    z-index: 1000;
    position: relative;
    display: flex;
}

.container_card div {
	display: inline-block;
    overflow: hidden;
    height: 100%;
    position: relative;
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container_card:hover figure{
	transform: perspective(600px) rotateY(180deg);
}

.container_card:hover figure img{
	filter: blur(2px);
}

.container_card:hover figure .trasera{
    opacity: 1;
}

figure{
	width: 250px;
	height: 300px;
	margin: 0;
	position: relative;
	transition: all ease .7s;
	transform-style: preserve-3d;
	transform: perspective(600px) rotateY(0deg);
    border-radius: 9px;
}

figure .frontal, figure .trasera{
	height: 100%;
	border-radius: 9px;
	transition: all ease .7s
}

figure .frontal{
	display: block;
	width: 250px;
	height: 300px;
}

figure .trasera{
	position: absolute;
	top: 0;
	color: white;
	transform: perspective(600px) rotateY(180deg);
  opacity: 0;
	overflow: auto;
	text-align: center;
	background: #38383855;
	background: rgba(51, 51, 51, 0.274);
	transition: all ease .7s;
	background-color: rgba(37, 35, 35, 0.876);
}

.trasera h2{
	background: rgb(93, 73, 73);
	text-align: center;
	line-height: 2;
	font-size: 27px;
	margin-bottom: 15px;
	margin-top: 15px;
	color: rgba(255, 255, 255, 0.911);
	text-shadow: .1px .1px 1px #000,.1px .1px 1px #000,-.1px -.1px 1px #000
}
.trasera hr{
	width: 90%;
	margin: auto;
	color: #aaa
}
.trasera p{
	margin-top: 10px;
	text-align: center;
	font-size: 22px;
	padding: 10px;
	color: rgb(200, 200, 200);
    font-weight: 700;
	letter-spacing: 1px;
	line-height: 1;
	font-weight: 100;
}

.container-responsive-cards{
	display:none
}

@media screen and (max-width:500px) {
	.Title-Projects{
		font-size: 31px;
		margin: 30px auto;
	}
	.container-cards{
		display: none;
	}
	.container-responsive-cards{
		position: relative;
		width: 85vw;
		display: block;
		margin: 20px auto;
		max-width: 400px;
		min-width: 200px;
	}
	.container_card-res{
		position: relative;
		width: 100%;
		display: flex;
		gap: 10px;
		background: #252534a6;
		background: #00000085;
		padding: 15px;
		box-sizing: border-box;
		text-align: center;
		border-radius: 166px 18px 90px;
		outline: 1px solid #252534;
		box-shadow: -1px -1px 1px rgb(0, 0, 0),-1px -1px 1px rgba(0, 0, 0, 0.952),-1px -1px 3px rgba(0, 0, 0, 0.918),-1px -1px 1px rgba(0, 0, 0, 0.842),-3px -3px 2px rgb(131, 75, 75);
	}

	.img-res{
		position: relative;
		height: 150px;
		min-width: 125px;
		height: 162px;
		min-width: 125px;
		border-radius: 7px;
		box-shadow: 1px 1px 1px rgb(0, 0, 0),1px 1px 1px rgba(0, 0, 0, 0.952),1px 1px 1px rgba(0, 0, 0, 0.918),1px 1px 1px rgba(0, 0, 0, 0.842),2px 2px 3px 1px rgb(0, 0, 0);
	}

	.h2-res{
		color: rgb(233, 187, 187);
		font-size: 23px;
	}
	.hr-res{
		margin: 6px;
		border: .1px solid rgb(92, 92, 92);
	}
	.p-res{
		color: #aaa;
		font-size: 16px;
	}
	.hr-res-cont{
		margin: 20px;
	}
	.links-projects-res{
		text-decoration: none;
	}
}

@media screen and (max-width:400px) {
	.container-responsive-cards{
		width: 99vw;
		min-width: 280px ;
		max-width: 325px;
	}

	.img-res{
		height: 152px;
		min-width: 115px
	}
	.h2-res{
		margin: 5px 0;
		font-size: 21px;
	}
	.hr-res{
		margin: 3px;
	}
	.p-res{
		font-size: 15px;
		margin: 1px;
	}
}
