.container-modal-PieChart {
    position:absolute; 
    top:400px;
    background:#5d7f4ef2;
    height: 140px;
    padding: 10px;
    z-index: 1200;
    border-radius: 3px;
    outline: 1px solid black;
    text-align: center;
}

.container-modal-PieChart form{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.container-modal-PieChart form p{
    font-weight: 800;
    font-size: 16px;
    letter-spacing: .5px;
}

.container-modal-PieChart form input{
    /* height: 25px; */
    padding: 4px 0px 4px 10px;
    border-radius: 5px;
    font-size: 12px;
}
.container-modal-PieChart form input[type="color"]{
    width: 20px;
    height: 20px;
    padding: 0;
    position: absolute;
    bottom: 20px;
    left: 15px;
}
.container-modal-PieChart form input[type="submit"]{
    /* height: 40px; */
    width: 100px;
    margin: auto;
    padding: 8px 0px;
    display: inline-block;

}

@media screen and (max-width:500px) {
    .container-modal-PieChart {
        top: 218px;
    }

    .container-modal-PieChart form input[type="color"]{
        left: 13px;
        bottom: 18px;
        width: 27px;
        height: 27px;
    }
}