.calendar .container-calendar{
    font-family:'Times New Roman';
    background-color: white;
    position: relative;
    width: 260px;
    height: 230px;
    box-sizing: border-box;
    padding: 20px 15px;
}
.calendar .container-calendar .month-year{
    margin-bottom: 10px;
}
.container-calendar ol{
    list-style:none;
    display: grid;
    grid-template-columns: repeat(7,1fr);
    grid-gap: 2px 0px;
    justify-items:center;
    margin: 0;
    padding: 0;
    margin-top: 6px;
}
.container-calendar li{
    font-size: 13px;
    text-align: center;
    padding: 4px 2px  0px 2px;
    border-radius: 50%;
    width: 21px;
    height: 21px;
}
.calendar .container-calendar .daysName{
    list-style: none;
    background-color: #5d7f4e85;
    border-radius: 0;
    display: grid;
    grid-gap: 2px 0px;
    justify-items:center;
    cursor: default;
    grid-template-columns: repeat(7,1fr);
}
.calendar .container-calendar .daysName li{
    font-size: 14px;
}
.container-calendar .today{
    color: white;
    background-color: #5d7f4e;
}
.container-calendar ol li:hover {
    cursor: default;
    color: white;
    background-color: #5d7f4e;
}
.container-calendar .firstDay{
    grid-column-start: 4;
}


.hidden{
    display: none;
}

.leaflet-container {
    width: 273px;
    height: 230px;
  }


  @media screen and (max-width:500px) {
    .container-dashboard{
        width: 100vw;
        height: 1400px;
        padding: 0 0 50px;
        margin-top: 0;
        margin-bottom: 0;
        height: 1450px;
        padding: 0px 0 40px;
    }
    .container-dashboard .container-data{
        display: inline-block;
        padding: 0;
        height: 100%;
    }
    .container-dashboard .container-data span{
        width: 100vw;
        /* background-color: red; */
        display: inline-block;
        position: relative;
        margin: 0;
        padding: 0;
    }
    .container-dashboard .container-data span h3{
        margin: 0;
        margin: 5px ;
    }
    .container-dashboard .container-data span p{
        margin: 5px;
    }
    .container-dashboard .container-data .statistic{
        width: 100vw;
        text-align: center;
        height: 1500px;
        /* background-color: red; */
        margin: 0 auto;
        position: relative;
        display: inline-block;
        padding-top: 15px;
    }
    .container-dashboard .container-data .items{
        max-width: 90vw;
        margin-bottom: 15px;
        /* background-color: green; */
    }
    .container-dashboard .container-data .items-2{
        width: 160px;
        margin: 5px;
        padding: 5px 10px;
    }
    .container-dashboard .container-data .statistic .items:nth-child(2){
        height: 170px;
    }
    .container-dashboard .container-data .statistic .items:nth-child(7){
        margin-top: 15px;
    }
    .header-dasboard nav{
        display: none
    }

}
