.title-contact{
    margin: 0 auto;
    font-size: 44px;
    color:rgb(223, 217, 217);
}
.cont-contact{
    margin: 0 auto;
    width: 50%;
    min-width: 340px;
    max-width: 650px;
    background: blue;
    padding: 20px;
    background: linear-gradient(#b2b2b24d,aquamarine);
    box-shadow: -2px 2px #111,-3px 3px #222,-4px 4px #333,-5px 5px #444, -6px 6px #555,-7px 7px #666,-8px 8px #333, -9px 9px #222, -10px 10px 10px #000;


background: #0000002e;
box-shadow: -2px 2px #1111117a,-3px 3px #22222270,-4px 4px #33333370,-5px 5px #444, -6px 6px #55555580,-7px 7px #6666666b,-8px 8px #3333336b, -9px 9px #22222270, -10px 10px 10px #0000006b;



background: #0000009e;
box-shadow: -2px 2px #1111117a,-3px 3px #22222270,-4px 4px #33333370,-5px 5px #444, -6px 6px #55555580,-7px 7px #6666666b,-8px 8px #3333336b, -9px 9px #22222270, -10px 10px 10px #0000006b;
color: #a6a6a6;
}
.name-contact{
    margin:0 auto;
    text-align: center;
    font-size: 35px;
    font-style: italic;
    margin-bottom: 25px;
}
.cont-contact span{
    font-size: 27px;
    margin-left: 30px;
    font-family: 'Adamina', serif;
    font-family: 'Yrsa', serif;
    padding: 5px;
}
.cont-contact span p{
    display: inline-block;
    margin-left: 10px;
    margin: 0px 0 14px 10px;
    font-style: italic;
}

@media screen and (max-width:500px) {
    .title-contact{
        font-size: 31px;
    }
    .cont-contact{
        width: 80%;
        min-width: 200px;
        padding: 10px;
    }
    .name-contact{
        font-size: 26px;
    }
    .cont-contact span{
        font-size: 19px;
    }
}

@media screen and (max-width:300px) {
    .cont-contact{
        width: 85%;
    } 
    .cont-contact span{
        margin-left: 15px;
    }
}