.footer{
    position: relative;
    height: 155px;
    background: rgba(36, 36, 49, 1);
    background: rgb(26, 26, 30);
    width: 100%;
    bottom: 0px;
}
.footer-container-enlaces{
    text-align: center;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-bottom: 35px;
    font-size: 33px;
    gap: 10px;
    color: #ada3a3;
    top: 32px;
}

.footer-container-enlaces a{
    color: #ada3a3;
    font-size: 38px;
}
.footer-container-enlaces a:hover{
    color: #dcd4d4;
}

.footer p{
    text-align: center;
    line-height: 1.3;
    bottom: 0;
    color: rgb(227, 221, 221);
    margin: 0;
    font-size: 17px;
}

@media screen and (max-width: 720px){
    .footer-container-enlaces a{
        font-size: 34px;
    }
    .footer p{
        position: relative;
        top: 3px;
    }
}

@media screen and (max-width: 400px){
    .footer{
        height: 144px;
    }
    .footer .footer-container-enlaces{
        top: 27px;
    }
    .footer p{
        top: -3px;
        font-size: 16px;
    }
    .footer .footer-container-enlaces a{
        font-size: 32px;
    }
}