.contenedor-calculator{
	background:linear-gradient(to left, #777,#7776);
	width:350px;
	height: 470px;
	border-radius: 15px;
	box-shadow: -2px 2px 1px #111,-2px 2px 1px #111,-2px 2px 1px #333,-2px 2px 1px #333,-2px 2px 1px #000,-2px 2px 1px #000,-25px 25px 1px #1119;
	position: relative;
	display:block;
	border-top: 1px solid #0004;
	border-right: 1px solid #0004;
	margin: 30px auto 40px;
}

.screen-calculator{
	position: relative;
	width: 90%;
	height: 16%;
	margin: 15px auto;
	display:block;
	background: #ddd;
	border-radius: 15px;
	outline: 2px solid #222;
	font-size: 37px;
	padding-top: 15px;
	padding-left: 15px;
	padding-right: 15px;
	box-sizing: border-box;
	font-family: "Century Schoolbook";
	overflow-y:none;
	overflow-x:auto;
}
.screen-calculator::-webkit-scrollbar{
	background: #bbb9;
	height: 10px;
	border-radius: 0px 0px 10px 10px;
}
.screen-calculator::-webkit-scrollbar-thumb{
	background: #5535;
	border-radius: 0px 0px 10px 10px;
	border-bottom: 1px solid #eee7
}
.btns{
	margin:20px;
	display: flex;
	flex-wrap: wrap;
	align-content: center;
	justify-content:center;
	justify-content: space-evenly; 
}
.btns .btn{
	margin: 0;
	padding: 0;
	width: 68px;
	height: 74px;
	text-align: center;
	display: flow-root;
	margin-top: 9px;
	border: 2px solid #020202c4;
	font-size: 37px;
	font-family: "Century Schoolbook","verdana";
	border-radius: 20px;
	box-shadow: -1px 1px 2px #444,-1px 1px 2px #444,-1px 1px 2px #444,-2px 2px 2px #3330,-2px 2px 2px #3337;
	color: black;
	background: #4443;
	background:linear-gradient(to left, #4441,#444);
	text-shadow:
	-.2px .2px 0px #444,-.2px .2px 0px #444,
	-.2px .2px 0px #444,-.2px .2px 0px #444,
	-.5px .2px 0px #444,-.5px .5px 0px #444,
	-.5px .5px 0px #444,-.5px .5px .2px #444,
	-.5px .5px .2px #444,-.5px .5px .5px #444,
	-.5px .5px .5px #444,-.5px .5px .5px #444,
	.2px -.2px 0px #aaa,.2px -.2px 0px #aaa,
	.2px -.2px 0px #aaa,.2px -.2px 0px #aaa,
	.2px -.2px 0px #aaa,.2px -.2px .2px #aaa,
	.2px -.2px .2px #aaa,.2px -.2px .2px #aaa;
	font-weight: normal;
}
.btns .btn:hover{
	background:linear-gradient(to left, #4442,#555);
}
.btns .btn:active{
	background:#424242;
	box-shadow:-2px 2px 1px #cccc;
	color: #aaaa;
}
.btns .btnClear{
	background: #e00a 
}
.btns .btnClear:hover, .btns .btnClear:active{
	background: #e00a 
}

@media screen and (max-width: 500px){
	.contenedor-calculator{
		margin: 5px auto;
		padding:0;
		box-sizing: border-box;
		border: none;
		width: 314px;
		position: relative;
		height: 90vh;
		max-height: 500px;
		max-width: 327px;
		min-width: 316px;
		height: 436px;
		padding-top: 4px;
		outline: .3px solid #0007;
	}
	.screen-calculator{
		position: relative;
		width: 90%;
		height: 13%;
		margin: 15px auto;
		margin-top: 15px;
		display: block;
		background: #ddd;
		border-radius: 15px;
		outline: 2px solid #222;
		font-size: 31px;
		padding-top: 12px;
		padding-left: 15px;
		padding-right: 15px;
		box-sizing: border-box;
		font-family: "Century Schoolbook";
		overflow-y: none;
		overflow-x: auto;
	}
	.btns .btn{
		width: 64px;
		height: 68px;
		font-size: 32px;
	}
}

@media screen and (max-width: 300px){
	.contenedor-calculator{
		margin: 0;
		padding:0;
		width: 100vw;
		max-height: 390px;
		min-width: 100vw;
		box-shadow: none
	}
	.screen-calculator{
		font-size: 30px;
	}
	.btns .btn{
		margin: 0;
		width: 24%;
		box-shadow: none;
		font-size: 25px;
		margin-top: 3px;
	}
	.btns .btn:active{
		box-shadow:-1px 1px 1px #cccc;
	}
}